* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: EB Garamond Variable, serif;
}

header {
  border-bottom: 1px solid #eee;
  flex-direction: column;
  align-items: center;
  padding: 1.5em;
  display: flex;
}

nav {
  margin-top: .8em;
  display: flex;
}

nav > a {
  padding: .5em;
  font-size: 16px;
}

canvas {
  border-radius: 20px;
}

.content {
  max-width: 600px;
  margin: 2em auto;
}

.square {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

